









































  .alpheios-alignment-editor-align-text-segment {
    span.alpheios-token {
        cursor: pointer;
        padding: 4px;
        border: 1px solid transparent;
        display: inline-block;

        &.alpheios-token-grouped {
          border-color: #BCE5F0;
          background: #BCE5F0;
        }

        &.alpheios-token-selected {
          border-color: #F27431;
          background: #F27431;
          color: #fff;
        }
    }
}
