





























































































.alpheios-alignment-editor-align-text-segment {
  position: relative;
  padding-top: 30px;

  .alpheios-align-text-segment__langname {
    position: absolute;
    overflow-y: hidden;
    border-bottom: 0;
    top: 0;
    right: 0;
    left: 0;
    margin: 0;
    padding: 0;
    text-align: right;
    padding: 5px;
    font-size: 90%;

    background: #185F6D;
    color: #fff;
    z-index: 100;
  }
}
