body, html {
  line-height: 1.5;
  margin: 0;
  padding: 0;
  min-height: 0;
  background-color: #e0e0e0;
  color: #000;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.alpheios-header {
  overflow: hidden;
  background-color: #6e6e6e;
  color: #FFFFFF;
  padding: 10px 10px 10px 25px;
}
.alpheios-header > div {
  display: inline-block;
  vertical-align: middle;
}
.alpheios-header .alpheios-header-logo .alpheios-logo-2 {
  display: inline-block;
}
.alpheios-header .alpheios-header-logo img {
  display: block;
  height: 100%;
  width: auto;
}
.alpheios-header .alpheios-header-title {
  margin-left: 10px;
}
.alpheios-header .alpheios-header-title h1 {
  padding: 0;
  margin: 0;
  font-weight: normal;
}

.alpheios-container-inner {
  padding: 0 10px;
}